<template>
  <div class="template-container">
    <div class="template-item-wrap" style="height: 50px;">
      <!-- 时间选择器 begin -->
      <div class="temp-item-leftblock">
        <el-date-picker @change="pickerDateChange" v-model="pickerDate" type="daterange" align="left" unlink-panels range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" :picker-options="pickerOptions2">
        </el-date-picker>
      </div>
      <!-- 时间选择器 end  -->
      <!-- 参数选择 begin -->
      <div class="temp-item-leftblock">
        <el-select v-model="adsParams.type" style="width:180px;" filterable>
          <el-option v-for="item in type" :key="item.value" :label="item.label" :value="item.value" >
          </el-option>
        </el-select>
      </div>
      <div class="temp-item-leftblock">
        <el-button type="primary" @click="exportExcel" >导出为excel</el-button>
      </div>
      <!-- 参数选择 end  -->
    </div>
    <div class="template-item-wrap">
      <div class="temp-item-inlineblock">
        <el-select v-model="adsParams.date_type" style="width:180px;" filterable>
          <el-option v-for="item in date_type" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="temp-item-inlineblock">
        <el-select v-model="adsParams.campaign_id" placeholder="请选择推广计划" style="width: 350px;"  filterable multiple remote collapse-tags :remote-method="remoteCampaign" :loading="loadingCampaign">
          <el-option v-for="item in defaults.campaignMap" :key="item.campaign_id" :label="item.campaign_name" :value="item.campaign_id">
          </el-option>
        </el-select>
      </div>
      <div class="temp-item-inlineblock">
        <el-select v-model="adsParams.adgroup_id" placeholder="请选择广告组" style="width:350px;" filterable multiple remote collapse-tags :remote-method="remoteAdgroup" :loading="loadingAdgroup">
          <el-option v-for="item in defaults.adgroupMap" :key="item.adgroup_id" :label="item.adgroup_name" :value="item.adgroup_id">
          </el-option>
        </el-select>
      </div>
      <div class="temp-item-inlineblock">
        <el-select v-model="adsParams.ad_id" placeholder="请选择广告" style="width:350px;" filterable multiple remote collapse-tags :remote-method="remoteAd" :loading="loadingAd">
          <el-option v-for="item in defaults.adMap" :key="item.ad_id" :label="item.ad_name" :value="item.ad_id">
          </el-option>
        </el-select>
      </div>
      <div class="temp-item-inlineblock">
        <el-select v-model="adsParams.product_id" placeholder="请选择标的物"  style="width:350px;" filterable multiple  collapse-tags>
          <el-option v-for="item in defaults.productMap" :key="item.product_refs_id" :label="item.product_name" :value="item.product_refs_id">
          </el-option>
        </el-select>
      </div>
      <div class="temp-item-inlineblock">
        <el-button type="primary" @click="searchList">查询</el-button>
      </div>
      
    </div>
    <!--<div class="tip"><p>效果趋势</p></div>-->
    <div class="tip"><p>效果详情</p></div>
    <div class="table-wrap" >
      <el-table id="export" v-if="tableBanner" ref="export" v-loading="loading" :data="listData" style="width: 100%" :default-sort="{prop: 'date', order: 'descending'}" >
         <tr v-for="(val, key) in tableBanner" :key="key" >
         <el-table-column v-if="key=='cost'" :prop="key" :label="val" :formatter="unitConversion"  sortable>
         </el-table-column>
         <el-table-column v-else-if="key=='date'" :fixed="true" :prop="key" :label="val"  sortable>
         </el-table-column>
         <el-table-column v-else :prop="key" :label="val"  sortable>
         </el-table-column>
        </tr>
      </el-table>
      <div class="table-pagination" v-if="page_info.total_number>0">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="page_info.page" :page-sizes="[10, 20, 30, 40]" :page-size="page_info.total_page" layout="total, sizes, prev, pager, next, jumper" :total="page_info.total_number">
        </el-pagination>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  created: function() {
    // if (this.account_id) {
    //   this.getParams()
    //   this.getReport(this.adsParams)
    // }
  },
  watch: {
    // 监听 account_id 发生变化
    account_id() {
      this.getParams()
      this.getReport(this.adsParams)
    }
  },
  computed: {
    account_id() {
      var account = this.$store.getters.get_account_action
      return account.account_id
    },
  },
  data() {
    return {
      adsParams: {
        page: 1,
        page_size: 10,
        type:'1',
        date_type: '1',
        campaign_id: '', 
        adgroup_id: '', 
        product_id:'', 
        ad_id:'', 
        start_date: '',
        end_date: ''
      },
      defaults:{
        campaignMap:[],
        adgroupMap:[],
        productMap:[],
        adMap:[],
      },
      loading: true,
      loadingAdgroup: false,
      loadingAd: false,
      loadingCampaign: false,
      listData: [],
      page_info: {},
      pickerDate: [],
      tableBanner:[],
      adCampaignsParams: {
        page: 1,
        page_size: 100,
        campaign_id: '',
        configured_status: '',
        campaign_name: '',
        start_date: '',
        end_date: ''
      },
      adGroupParams:{
        page: 1,
        page_size: 100,
        adgroup_id: '',
        configured_status: '',
        adgroup_name: '',
        start_date: '',
        end_date: ''
      },
      adParams:{
        page: 1,
        page_size: 100,
        ad_id: '',
        configured_status: '',
        ad_name: '',
        start_date: '',
        end_date: ''
      },
      pickerOptions2: {
        shortcuts: [
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
              picker.$emit('pick', [start, end])
            }
          }
        ]
      },
      type: [
        {
          value: '1',
          label: '效果',
        },
        {
          value: '2',
          label: 'APP转化效果'
        }
      ],
      date_type: [
        {
          value: '1',
          label: '天'
        },
        {
          value: '2',
          label: '小时'
        },
      ],
    }
  },
  methods: {
    formatterSystemStatus(row, column) {
      return this.system_status[row.system_status]
    },
    unitConversion(row, column) {
      return row.cost / 100
    },
    searchList() {
      this.getReport(this.adsParams)
    },
    handleSizeChange(val) {
      this.adsParams.page_size = val
      this.getReport(this.adsParams)
      // console.log(`每页 ${val} 条`)
    },
    handleCurrentChange(val) {
      this.adsParams.page = val
      this.getReport(this.adsParams)
      //console.log(`当前页: ${val}`)
    },
    pickerDateChange() {
      var start_date = new Date(this.pickerDate[0]),
        end_date = new Date(this.pickerDate[1])
      this.adsParams.start_date =
        start_date.getFullYear() +
        '-' +
        (start_date.getMonth() + 1) +
        '-' +
        start_date.getDate()
      this.adsParams.end_date =
        end_date.getFullYear() +
        '-' +
        (end_date.getMonth() + 1) +
        '-' +
        end_date.getDate()
    },
    getReport: function(params) {
      params.account_id = this.account_id
      var that = this
      this.$axios
        .get('/Admin/Marketing_Report/getData', {
          params: params
        })
        .then(function(response) {
          that.loading = false
          var respData = response.data
          if (respData.code == 0) {
            if (respData.data.list || respData.data.page_info) {
              that.tableBanner = respData.data.table_banner
              that.listData = respData.data.data
              that.page_info = respData.data.page_info
            }
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          that.loading = false
        })
    },

    //获得select各类默认参数
    getParams:function(){
      var that = this
      this.$axios.get('/Admin/Marketing_Report/getParams',{params:{account_id:this.account_id}
      }).then(function(response){
         that.loading = false
         var respData = response.data
         that.defaults.campaignMap = respData.data.campaignMap
         that.defaults.productMap = respData.data.productMap
         that.defaults.adgroupMap = respData.data.adGroupMap
         that.defaults.adMap = respData.data.adsMap
         that.pickerDate =respData.data.dateRange
         that.adsParams.start_date = respData.data.dateRange[0]
         that.adsParams.end_date = respData.data.dateRange[1]
      }).catch(function(err){
         that.$message('网络繁忙，请稍后再试！')
          that.loading = false
      })
    },


    //获取广告计划
    remoteCampaign:function(query){
      // if (query !== '') {
        this.loadingCampaign = true;
        setTimeout(() => {
          this.adCampaignsParams.campaign_name = query
          this.getAdCampaigns(this.adCampaignsParams)
        }, 300);
      // } else {
      //   that.defaults.campaignMap = [];
      // }
    },
    getAdCampaigns: function(params) {
      params.account_id = this.account_id
      var that = this
      this.$axios
        .get('/Admin/Marketing_Campaigns/get', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            if (respData.data.list || respData.data.page_info) {
              that.defaults.campaignMap = respData.data.list
              that.loadingCampaign = false
              //console.log(that.adCampaignsList)
              //that.page_info = respData.data.page_info
            }
          } else {
            that.$message(respData.msg)
            that.loadingCampaign = false
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          that.loadingCampaign = false
        })
    },

    //远程获取广告组
    remoteAdgroup:function(query){
        // if (query !== '') {
          this.loadingAdgroup = true;
          setTimeout(() => {
            this.adGroupParams.adgroup_name = query
            this.getAdGroups(this.adGroupParams)
          }, 300);
        // } else {
        //   this.defaults.adgroupMap = [];
        // }
    },
    getAdGroups: function(params) {
      params.account_id = this.account_id
      var that = this
      this.$axios
        .get('/Admin/Marketing_Adgroups/get', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            if (respData.data.list || respData.data.page_info) {
              that.defaults.adgroupMap = respData.data.list
              that.loadingAdgroup = false
              //console.log(that.adCampaignsList)
              //that.page_info = respData.data.page_info
            }
          } else {
            that.$message(respData.msg)
            that.loadingAdgroup = false
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          that.loadingAdgroup = false
        })
    },


    //远程获取广告
    remoteAd:function(query){
      //  if (query !== '') {
          this.loadingAd = true;
          setTimeout(() => {
            this.adParams.ad_name = query
            this.getAd(this.adParams)
          }, 300);
        // } else {
        //   this.defaults.adMap = [];
        // }
    },
    getAd:function(params) {
      params.account_id = this.account_id
      var that = this
      this.$axios
        .get('/Admin/Marketing_Ads/get', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            if (respData.data.list || respData.data.page_info) {
              that.defaults.adMap = respData.data.list
              that.loadingAd = false
              //console.log(that.adCampaignsList)
              //that.page_info = respData.data.page_info
            }
          } else {
            that.$message(respData.msg)
            that.loadingAd = false
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          that.loadingAd = false
        })
    },
    //导出2
    exportExcel:function(e) {
      var uri = 'data:application/vnd.ms-excel;base64,',text='',
      template = '<html><head><meta charset="UTF-8"></head><body><table border="1">{table}</table></body></html>',
      base64 = function(s) { return window.btoa(unescape(encodeURIComponent(s))) },
      format = function(s, c) {
          return s.replace(/{(\w+)}/g,
              function(m, p) { return c[p]; })
      }
      // console.log(this.$refs.export.$vnode.elm.children[3].innerHTML);
      // return false;
      text +=this.$refs.export.$vnode.elm.children[3].innerHTML
      var ctx = {worksheet: 'sheet1', table: text}
      window.location.href = uri + base64(format(template, ctx))
      e.stopPropagation();
    },
  },
}
</script>


<style scoped>
.temp-item-leftblock{
  float: right;
  margin-left:10px;
}
.tip{
    padding: 8px 16px;
    background-color: #ecf8ff;
    border-radius: 4px;
    border-left: 5px solid #50bfff;
    margin: 20px 0;
}   

</style>



